<template>
  <div class="wrap">
    <div class="head">
      <van-icon @click="left" name="arrow-left" />
      <p>我的地址</p>
      <p @click="xin">新建地址</p>
    </div>
    <ul class="content">
      <li v-for="item in list" :key="item.id">
        <van-checkbox-group v-model="result" ref="checkboxGroup">
          <van-checkbox name="a"></van-checkbox>
        </van-checkbox-group>
        <div class="wenb">
          <div class="title1">
            <p>{{ item.linkMan }}</p>
            <p>{{ item.mobile }}</p>
          </div>
          <p>
            <span></span>
            <span>{{ item.provinceStr }}</span>
            <span>{{ item.address }}</span>
          </p>
        </div>

        <van-icon class="you" name="share" />
      </li>
    </ul>
  </div>
</template>

<script>
import qs from "qs";
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
      result: [],
      list: []
    };
  },
  created() {
    //创建后
    let d = { token: localStorage.getItem("token") };
    let data = qs.stringify(d);
    this.$http
      .post("https://api.it120.cc/small4/user/shipping-address/list", data)
      .then(msg => {
        console.log(msg);
        if (msg.data.msg == "success") {
          this.list = msg.data.data;
        }
      });
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    left(){
        this.$router.go(-1)
    },
    xin(){
        this.$router.push("/newdiz")
    }
  }
};
</script>

<style scoped lang="less">
.wrap {
  font-size: 0.2rem;
  .head {
    display: flex;
    height: 0.7rem;
    align-items: center;
    justify-content: space-between;
    padding: 0 0.2rem;
    box-sizing: border-box;
    
  }
  .content {
    > li {
      display: flex;
      justify-content: left;
      height: 0.9rem;
      align-items: center;
      padding: 0 0.2rem;
      box-sizing: border-box;
      border-bottom: 0.01rem solid #f5f5f5;
      .wenb {
        display: flex;
        height: 0.9rem;
        flex-direction: column;
        justify-content: space-around;
        margin-left: 0.2rem;
        .title1 {
            display: flex;
        }
      }
      .you {
        position: fixed;
        right: 0.3rem;
        font-size: 0.3rem;
        color: #999;
      }
    }
  }
}
</style>



