import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        title: 'hello sqw',
        cart: []
    },
    mutations: {
        addCart(state, goods) {
            // state.cart.push(goods)

            let n = state.cart.findIndex((item) => {
                return item.id == goods.id
            })
            // console.log(n)
            if (n == -1) {
                state.cart.push(goods)
            } else {
                let cart = state.cart;
                cart[n].num += goods.num
                state.cart = cart
            }
        },
        dianj(state, index) {
            state.cart[index].checked = !state.cart[index].checked
        },
        // +方法
        // ch(state, d) {
        //     let cart = state.cart
        //     cart[d.index].num = d.value
        //     state.cart = cart
        // },
        del(state) {
            // let flag = false
            // for (var i = 0; i < state.cart.length; flag ? i : i++) {
            //     if (state.cart[i].checked == true) {
            //         state.cart.splice(i, 1)
            //         flag = true
            //     } else {
            //         flag = false
            //     }
            // }
            state.cart = state.cart.filter((item)=>{
                return !item.checked
            })
        },
        jia(state,k){
            state.cart[k].num++
        },
        jian(state,k){
            state.cart[k].num--
        },
    },
    actions: {

    },
    getters: {
        nums(state) {
            let sum = 0;
            state.cart.forEach((item) => {
                sum += item.num
            })
            return sum
        },
        jiag(state) {
            let sum = 0;
            state.cart.forEach((item) => {
                if (item.checked) {
                    sum += item.num * item.price
                }
            })
            return sum
        },
        qrdd(state){
            return state.cart.filter((item)=>{
                return item.checked
            })
        }
    },
    plugins: [createPersistedState()]
})



