<template>
    <div class="tjlist" @click="tuij(tj.id)">
        <img :src="tj.pic" alt="">
        <p class="title">{{ tj.name }}</p>
        <p class="neir">{{ tj.characteristic }}</p>
        <p class="jiag">￥{{ tj.minPrice }}</p>
    </div>
</template>

<script>
export default {
    props:["tj"],
    data() {
        return {
            
        };
    },
    created() {

    },
    mounted() {

    },
    methods: {
        tuij(id){
            this.$router.push("/fenldetail?id="+id)
        }
    }
};
</script>

<style scoped lang="less">
.tjlist{
    display: flex;
    flex-direction: column;
    
    img{
        width:2.33rem;
        height: 3rem;
    }
    .title{
        font-size:0.2rem;
        width: 2rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin-top: 0.05rem;
    }
    .neir{
        font-size:0.14rem;
        width: 2rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        color: #C7C7C7;
        padding: 0.1rem 0;
    }
    .jiag{
        font-size:0.16rem;
        color: #D00000;
    }
}
</style>



