<template>
  <div class="wrap">
    <div class="box">
      <p class="title">登录账号</p>
      <p class="zi">虾米-严选商城欢迎您</p>
      <p class="wenb" :class="{'action':gais,'action1':gais1 }">
        <input type="text" placeholder="手机号码" v-model="phone" @blur="phone1" />
        <span style="color: red;" v-show="bit">必填</span>
        <van-icon name="graphic" />
      </p>
      <p class="wenbtwo" :class="{'action':mimg,'action1':mimg1 }">
        <input type="password" placeholder="密码" v-model="pwd" v-if="mixian" @blur="mim" />
        <input type="text" placeholder="密码" v-model="pwd" v-else />
        <span style="color: red;" v-show="bit1">必填</span>
        <van-icon name="closed-eye" @click="dianx" v-if="mixian" />
        <van-icon name="eye-o" @click="diany" v-else />
      </p>
      <p class="ann" @click="login">登录</p>
      <p class="wmm">忘记密码</p>
      <p class="wei">
        还没有注册？
        <span @click="zhuc">立即注册</span>
      </p>
    </div>
  </div>
</template>


<script>
import qs from "qs";
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
      phone: "",
      pwd: "",
      mixian: true,
      //手机号改变色
      gais: false,
      gais1: false,
      //密码改变色
      mimg: false,
      mimg1: false,
      //必填
      bit: false,
      bit1: false,
      p1: ""
    };
  },
  created() {
    //创建后
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
  },
  beforeRouteEnter(to, from, next) {
    console.log(from);
    next(vm => {
      vm.p1 = from.fullPath;
    });
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    login() {
      let data = {
        mobile: this.phone,
        pwd: this.pwd
      };
      this.$http
        .post(
          "https://api.it120.cc/small4/user/m/login?deviceId=007&deviceName=monkey",
          qs.stringify(data)
        )
        .then(msg => {
          console.log(msg);
          if (msg.data.msg == "success") {
            localStorage.setItem("token", msg.data.data.token);

            //如果点击加购 先登录 登录后在跳转回详情
            if (this.p1 != "") {
              this.$router.push(this.p1);
            } else {
              this.$router.push("/home/");
            }

            // this.$router.push("/home/ger");
          } else {
            // 警告通知
            this.$notify({ type: "danger", message: "请先输入账号密码" });
          }
        });
    },
    dianx() {
      this.mixian = false;
    },
    diany() {
      this.mixian = true;
    },
    //手机号验证是否为空
    phone1() {
      if (this.phone == "") {
        // 警告通知
        this.gais = true;
        this.bit = true;
        this.$notify({ type: "danger", message: "请输入正确的手机号" });
      } else if (this.phone != "") {
        this.gais1 = true;
      }
    },
    //密码验证是否为空
    mim() {
      if (this.pwd == "") {
        // 警告通知
        this.mimg = true;
        this.bit1 = true;
        this.$notify({ type: "danger", message: "请输入正确密码" });
      } else if (this.pwd != "") {
        this.mimg1 = true;
      }
    },
    //点击注册跳转注册页
    zhuc() {
      this.$router.push("/zhuce");
    }
  }
};
</script>

<style scoped lang="less">
.wrap {
  padding: 0.26rem;
  .box {
    width: 3.8rem;
    border: 0.01rem solid #f5f5f5;
    padding: 0 0.44rem;
    font-size: 0.2rem;
    min-height: 9rem;
    .title {
      margin-top: 0.5rem;
      font-size: 0.3rem;
    }
    .zi {
      font-size: 0.2rem;
      color: #909090;
      margin-top: 0.5rem;
      margin-bottom: 0.23rem;
    }
    .wenb {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.44rem;
      display: flex;
      justify-content: space-around;
      align-items: center;
      border: 0.01rem solid #f5f5f5;
      input {
        border: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        outline: none;
      }
      .van-icon {
        font-size: 0.22rem;
        color: #d1cfce;
      }
    }
    .action {
      border: 0.01rem solid rgb(225, 44, 30) !important;
      // background: rgb(255, 233, 228) !important;
    }
    .action1 {
      border: 0.01rem solid rgb(56, 219, 36) !important;
      background: rgb(225, 255, 223) !important;
      input {
        background: rgb(225, 255, 223) !important;
      }
    }
    .wenbtwo {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      justify-content: space-around;
      align-items: center;
      border: 0.01rem solid #f5f5f5;
      input {
        border: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        outline: none;
      }
      .van-icon {
        font-size: 0.22rem;
        color: #d1cfce;
      }
    }
    .ann {
      width: 2.91rem;
      height: 0.68rem;
      background: linear-gradient(135deg, #e570e7 0%, #79f1fc 100%);
      text-align: center;
      line-height: 0.68rem;
      margin: 0 auto;
      margin-top: 0.24rem;
      color: #fff;
      border-radius: 0.06rem;
      letter-spacing: 0.06rem;
    }
    .wmm {
      text-align: center;
      margin-top: 0.2rem;
      color: #989898;
    }
    .wei {
      width: 2.91rem;
      font-size: 0.22rem;
      color: #549ff9;
      margin: 0 auto;
      text-align: center;
      margin-top: 0.32rem;
    }
  }
}
</style>



