<template>
  <div class="wrap">
    <div class="head">
      <van-icon name="arrow-left" @click="back" />
      <p class="title">分类商品</p>
      <p></p>
    </div>
    <ul class="xiangq">
        <li v-show="lbjlist.length!=0" v-for="(item) in lbjlist" :key="item.id">
            <img @click="xq(item.id)" :src="item.pic" alt="">
            <p class="wenz">{{ item.name }}</p>
            <p class="price">￥{{ item.originalPrice }}</p>
        </li>
        <img v-show="lbjlist.length==0" class="tupian" src="static/img1/tup5.png" alt="">
    </ul>
  </div>
</template>

<script>
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
      f: false,
      lbjlist:[]
    };
  },
  created() {
    //创建后
    // this.$route.query.id 
    // alert(this.$route.query.id )
    this.$http.get("https://api.it120.cc/small4/shop/goods/list").then((msg)=>{
        // console.log(msg)
            if(msg.data.msg=="success"){
                this.lbjlist=msg.data.data.filter((item)=>{
                    return item.categoryId==this.$route.query.id
                })
                
            }
        

    })

  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    back() {
      this.$router.go(-1);
    },
    xq(id){
        this.$router.push("/fenldetail?id="+id)
    }
  }
};
</script>

<style scoped lang="less">
.wrap{
    .head{
        width: 100%;
        height: 0.7rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .van-icon{
            font-size: 0.24rem;
            padding-left: 0.24rem;
        }
        .title{
            font-size: 0.2rem;
        }
    }
    .xiangq{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        padding: 0 0.1rem;
        box-sizing: border-box;
        >li{
            width: 2.45rem;
            height: 2.45rem;
            position: relative;
          
            >img{
                width: 100%;
                height: 100%;
            }
            >.wenz{
                width: 2.45rem;
                font-size: 0.14rem;
                position: absolute;
                top: 1.8rem;
                left: 0rem;
                z-index: 20;
                background: #F0EDD4;
                padding: 0.2rem 0;
                color: #B69B72;
                padding-left: 0.04rem;
            }
            >.price{
                font-size: 0.2rem;
                color: #CF1311;
            }
        }
        .tupian{
            width: 100%;
            margin-top: 0.8rem;
        }
    }

}
</style>



