<template>
  <div class="wrap">
    <p class="search">
      <!-- 搜索对应商品 -->
      <input v-model="search" @keydown.13="searchAdd" type="search" placeholder="回车搜索商品" />
    </p>
    <!-- 下侧内容部分 -->
    <div class="content">
      <!-- 左侧 -->
      <ol class="left">
        <li @click="all" :class="{active:num==0}">全部分类</li>
        <li
          :class="{active:(index+1)==num}"
          v-for="(item,index) in lbjfenl"
          :key="item.id"
          @click="getData(item.id,(index+1))"
        >{{ item.name }}</li>
      </ol>
      <!-- 右侧 -->
      <div class="right">
        <!-- 轮播 -->
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
          <van-swipe-item v-for="(item) in lunb" :key="item.id">
            <img :src="item.img" alt />
          </van-swipe-item>
        </van-swipe>
        <!-- 右边下册与左侧的对应数据 -->
        <ul class="conlist">
          <li v-for="(item) in lbjfenl1" :key="item.id" @click="Fenldetail(item.id)">
            <img :src="item.icon" alt />
            <p>{{ item.name }}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
      lbjfenl: [], //数组1
      lbjfenl1: [], //数组2
      lbjfenl2: [], //数组3
      num: 0, //初始化样式
      lunb: [
        { img: "static/img1/tup1.jpg" },
        { img: "static/img1/tup2.jpg" },
        { img: "static/img1/tup3.jpg" },
        { img: "static/img1/tup4.jpg" }
      ],
      search:'',
      newshuj:[]
    };
  },
  created() {
    //创建后
    this.getZong();
    this.all();
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    async getZong() {
      let shuj = await this.$http.post(
        "https://api.it120.cc/small4/shop/goods/category/all"
      );
      //判断数据里的msg为success时 过滤出level为1的数据
      if (shuj.data.msg == "success") {
        let shuj1 = shuj.data.data.filter(res => {
          return res.level == 1;
        });
        this.lbjfenl = shuj1; //将过滤出的数据赋值给数组1
        this.lbjfenl2 = shuj.data.data; //数组3显示的是所有数据
      }
    },
    //左侧
    getData(id, index) {
      this.num = index; //把索引赋值给num
      //过滤出全部里的pid==id的
      let lbjarr = this.lbjfenl2.filter(item => {
        return item.pid == id;
      });
      //将过滤出来的赋值给数组2
      this.lbjfenl1 = lbjarr;
    },
    //所有的
    async all() {
      let shuj = await this.$http.post(
        "https://api.it120.cc/small4/shop/goods/category/all"
      );
      this.lbjfenl1 = shuj.data.data;
      //点击all右侧显示所有分类
      // console.log(this.lbjfenl1);
      this.num = 0; //设置激活样式
    },
    //回车搜索商品
    searchAdd(){
        this.newshuj=[]
        for(let i in this.lbjfenl){
            if(this.lbjfenl[i].name.indexOf(this.search)>=0){
                this.newshuj.push(this.search[i])
            }
        }
    },
    //点击跳转至分类里的分类商品
    Fenldetail(id){
      this.$router.push("/fenlitem?id="+id)
    }
  }
};
</script>

<style scoped lang="less">
.wrap {
  font-size: 0.14rem;
  .search {
    width: 100%;
    height: 0.5rem;
    border-bottom: 0.01rem solid #f3f3f3;
    display: flex;
    align-items: center;
    padding-left: 0.2rem;
    box-sizing: border-box;
    input {
      width: 3.47rem;
      height: 0.35rem;
      background: #f5f5f5;
      border: none;
      border-radius: 0.2rem;
      padding-left: 0.2rem;
      font-size: 0.14rem;
    }
  }
  .active {
    border-left: 0.03rem solid red;
    color: #d41515;
  }
  .content {
    display: flex;
    .left {
      width: 1.27rem;
      background: #fff;
      height: 100%;
      position: fixed;
      left: 0;
      top: 0.51rem;
      border-right: 0.01rem solid #e9e9e9;
      > li {
        width: 1.27rem;
        height: 0.5rem;
        font-size: 0.2rem;
        text-align: center;
        line-height: 0.5rem;
        margin-top: 0.1rem;
      }
    }
    .right {
      width: 3.92rem;
      height: 80%;
      position: fixed;
      right: 0;
      top: 0.51rem;
      overflow: auto;

      //   padding-bottom: 0.1rem;
      .van-swipe {
        width: 3.4rem;
        overflow: hidden;
        margin: 0 auto;
        .van-swipe-item {
          width: 100%;
          margin-top: 0.1rem;

          > img {
            width: 3.4rem;
            height: 1.8rem;
            border-radius: 0.1rem;
          }
        }
      }
      .conlist {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        li {
          width: 30%;
          > img {
            width: 1.1rem;
            height: 1.1rem;
            margin-top: 0.1rem;
          }
          > p {
            font-size: 0.16rem;
            color: #3e4561;
            text-align: center;
            margin-top: 0.1rem;
          }
        }
      }
    }
  }
}
</style>



