<template>
  <div class="wrap">
    <div class="tou">
      <p class="bianj" @click="bj" v-show="bianj" >编辑</p>
      <p class="bianj" @click="bj" v-show="!bianj">完成</p>
      <p>购物车</p>
      <p></p>
    </div>
    <div v-if="list.length!==0">
      <ul class="jiagsj">
        <li v-for="(item,index) in list" :key="index">
          <!-- <van-checkbox-group v-model="result" ref="checkboxGroup">
            <van-checkbox name="a" checked-color="#AB2929"></van-checkbox>
          </van-checkbox-group>-->
          <input type="checkbox" @click="fux(index)" v-model="item.checked" />
          <!-- <van-checkbox @click="fux(index)" v-model="item.checked" checked-color="red"></van-checkbox> -->
          <img :src="item.pic" alt />
          <div class="title">
            <p class="wenz">{{ item.name }}</p>
            <p class="guig" v-for="(i) in item.pro" :key="i.id">{{ i.name }}</p>
            <div class="price">
              <span>￥{{ item.price }}</span>
              <!-- <p class="ann">
                <button>-</button>
                <input type="text" value="1">
                <button>+</button>
              </p>-->
              <!-- <van-stepper v-model="item.num" @change="ch(index)"  /> -->
              <van-stepper v-model="item.num" @minus="jian(index)" @plus="jia(index)"  />
            </div>
          </div>
        </li>
      </ul>
    </div>
    <p class="tu" v-else>
      <img src="static/img1/tup6.png" alt />
    </p>
    <div class="tabdi">
      <!-- <input type="checkbox" />
      <span class="qx">全选</span>-->

      <!-- <van-checkbox v-model="checked" checked-color="#AB2929"></van-checkbox>
      <van-button type="info" @click="toggleAll">全选</van-button>-->
      <input type="checkbox" v-model="checked" @click="quanx" />全选
      <span class="hj">合计：￥{{ $store.getters.jiag }}</span>
      <button class="qieh" v-show="bianj" @click="goorder">下单</button>
      <button class="qieh"  v-show="!bianj" @click="del">删除</button>
      <!-- <van-submit-bar :price="3050" button-text="提交订单" @submit="onSubmit">
        <van-checkbox v-model="checked">全选</van-checkbox>
        <template #tip>
          你的收货地址不支持同城送,
          <span @click="onClickEditAddress">修改地址</span>
        </template>
      </van-submit-bar>-->
    </div>
    <Tabber></Tabber>
  </div>
</template>

<script>
import Tabber from "@/pages/Tabber";
import { mapState } from "vuex";

export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
      bianj: true,
      value: 1,
      //复选框
      result: [],
      checked: false
    };
  },
  created() {
    //创建后
    // console.log(this.list);
  },
  components: {
    Tabber
  }, // 局部注册的组件
  mounted() {
    //加载后
  },
  watch: {}, // 负责监听
  computed: {
    // goods(){
    //   return this.$store.state.cart
    // }
    ...mapState({
      list: state => state.cart,
      t: state => state.title
    })
  }, // 计算属性
  methods: {
    //方法集合
    // ...mapMutations(["ch"]),

    bj() {
      this.bianj = !this.bianj;
    },
    toggleAll() {
      this.$refs.checkboxGroup.toggleAll();
    },
    fux(index) {
      console.log(this.list[index].checked);
      this.$store.commit("dianj", index);
      this.checked = this.list.every(item => {
        return item.checked == true;
      });
    },
    quanx() {
      this.checked = !this.checked;
      this.list.forEach(item => {
        item.checked = this.checked;
      });
      if(this.list==null){
        this.checked=false
      }
    },
    ch(n) {
      this.$store.commit("ch",{index:n,value:this.list[n].num})
    },
    //删除
    del(){
      this.$store.commit("del")
    },
    jia(k){
      this.$store.commit("jia",k)
    },
    jian(k){
      this.$store.commit("jian",k)
    },
    goorder(){
      this.$router.push("/Qrdd")
    }
  }
};
</script>

<style scoped lang="less">
.wrap {
  font-size: 0.2rem;
  .tou {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 0.7rem;
    font-size: 0.22rem;
    padding: 0 0.1rem;
    box-sizing: border-box;
    border-bottom: 0.01rem solid #e9e9e9;
    .bianj {
      color: #d00000;
    }
  }
  .tu {
    img {
      width: 100%;
    }
  }
  .jiagsj {
    width: 100%;
    // padding-bottom: 2rem;
    position: fixed;
    height: 80%;
    overflow: auto;
    > li {
      width: 100%;
      display: flex;
      padding: 0.2rem 0.1rem;
      // height: 2em;
      align-items: center;
      box-sizing: border-box;
      > input {
        margin-right: 0.2rem;
      }
      > img {
        width: 1.3rem;
        height: 1.3rem;
        border-radius: 0.04rem;
      }
      .title {
        display: flex;
        flex-direction: column;
        margin-left: 0.2rem;
        height: 1.3rem;
        justify-content: space-between;

        .wenz {
          font-size: 0.18rem;
        }
        .guig {
          font-size: 0.14rem;
          color: #b2b2b2;
        }
        .price {
          width: 3.09rem;
          display: flex;
          justify-content: space-between;
          height: 0.5rem;
          align-items: center;
          font-size: 0.18rem;
          color: #d00000;
          .van-stepper {
            .van-stepper__input {
              width: 40px;
            }
          }
          .ann {
            button {
              color: #c8c9cc;
              width: 0.33rem;
              height: 0.33rem;
              border: none;
              outline: none;
            }
            > input {
              width: 0.4rem;
              height: 0.32rem;
              border: none;
              background: #f2f3f5;
              color: #000;
              text-align: center;
              line-height: 0.32rem;
            }
          }
        }
      }
    }
  }
  .tabdi {
    width: 100%;
    display: flex;
    height: 0.68rem;
    border-top: 0.01rem solid #e9e9e9;
    border-bottom: 0.01rem solid #e9e9e9;
    align-items: center;
    padding-left: 0.16rem;
    position: fixed;
    bottom: 0.5rem;
    left: 0;
    background: #fff;
    // position: relative;
    .van-submit-bar {
      bottom: 0.65rem;
      // .van-checkbox__icon--checked .van-icon {
      //   background: #ab2929;
      // }
    }

    .van-button {
      font-size: 0.18rem;
      margin-left: 0.08rem;
      border: none;
      background: #fff;
      color: #000;
    }
    .hj {
      font-size: 0.18rem;
      margin-left: 1.4rem;
      color: #d00000;
      margin-right: 0.3rem;
    }
    .qieh {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 1.66rem;
      height: 0.68rem;
      font-size: 0.2rem;
      background: #d00000;
      color: #fff;
      outline: none;
      border: none;
      text-align: center;
      line-height: 0.68rem;
    }
  }
}
</style>



