<template>
  <div class="wrapper">
    <van-loading color="#1989fa" v-if="loading" />
    <div class="head" v-else>
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="item in pics" :key="item.id">
          <img :src="item.pic" alt />
        </van-swipe-item>
      </van-swipe>
      <div class="fanh">
        <p>
          <van-icon name="arrow-left" @click="back" />
        </p>
      </div>

      <van-loading color="#1989fa" v-if="loading" />

      <div class="biaot" v-else>
        <p class="miaos">{{ lbjtitle.name }}</p>
        <p class="jies">{{ lbjtitle.characteristic }}</p>
        <div class="price">
          <p class="dij">
            低价
            <span>￥{{ lbjtitle.minPrice }}</span>
            <span>原价￥{{ lbjtitle.originalPrice }}</span>
          </p>
          <p class="kc">库存{{ lbjtitle.stores }}</p>
        </div>
      </div>
    </div>
    <!-- <div class="jies1"> -->

    <van-loading color="#1989fa" v-if="loading" />
    <van-tabs @click="onClick" v-else>
      <van-tab title="商品介绍">
        <ul class="content">
          <li v-html="lbjlist.content">
            <img src alt />
            <p></p>
          </li>
        </ul>
      </van-tab>
      <van-tab title="商品评价">
        <ul class="pingj">
          <li v-for="item in lbjshuj" :key="item.id">
            <i class="van-icon van-icon-user-circle-o"></i>
            <div>
              <div>
                <p>{{ item.user.nick==null?'匿名用户': item.user.nick }}</p>
                <p>{{ item.goods.goodReputationStr }}</p>
              </div>
              <p>{{ item.goods.goodReputationRemark }}</p>
              <div>
                <p>{{ item.goods.dateReputation }}</p>
                <p>
                  选择规格:
                  <span>{{ item.goods.property }}</span>
                </p>
              </div>
            </div>
          </li>
        </ul>
      </van-tab>
    </van-tabs>
    <!-- 底部 -->
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" color="#828282" />
      <van-goods-action-icon icon="cart-o" color="#828282" @click="gouwuc" :badge="$store.getters.nums" />
      <van-goods-action-icon icon="star-o" color="#828282" @click="xingx" v-if="shouc" />
      <van-goods-action-icon icon="star" color="#F7BA2A" @click="xingx" v-else />
      <van-goods-action-button type="warning" @click="showPopup" text="加入购物车" />
      <van-popup v-model="show" closeable position="bottom">
        <van-loading color="#1989fa" v-if="loading" />
        <div class="gouwc-detail" v-else>
          <img class="detail-tu" :src="lbjlist.basicInfo.pic" alt />
          <div class="detail-title">
            <p>{{ lbjlist.basicInfo.name }}</p>
            <p>￥{{ lbjlist.basicInfo.minPrice }}</p>
          </div>
        </div>
        <div class="lieb">
          <div class="lieb-ming" v-for="(item,index1) in ps" :key="item.id">
            <p>{{ item.name }}</p>
            <div class="lieb-leib">
              <div
                v-for="(p2,index2) in item.childsCurGoods"
                :class="{'active':p2.checked}"
                @click="select(index1,index2)"
                :key="p2.id"
              >{{ p2.name }}</div>
            </div>
          </div>
        </div>
        <div class="goumsl">
          <p>购买数量</p>
          <van-stepper v-model="value" />
        </div>
        <div class="shop">
          <button @click="jiag">加入购物车</button>
        </div>
      </van-popup>

      <van-goods-action-button type="danger" @click="showPopup2" text="立即购买" />
      <van-popup v-model="show2" closeable position="bottom">
        <van-loading color="#1989fa" v-if="loading" />
        <div class="gouwc-detail" v-else>
          <img class="detail-tu" :src="lbjlist.basicInfo.pic" alt />
          <div class="detail-title">
            <p>{{ lbjlist.basicInfo.name }}</p>
            <p>￥{{ lbjlist.basicInfo.minPrice }}</p>
          </div>
        </div>

        <div class="lieb">
          <div class="lieb-ming" v-for="(item,index1) in ps" :key="item.id">
            <p>{{ item.name }}</p>
            <div class="lieb-leib">
              <div
                v-for="(p2,index2) in item.childsCurGoods"
                class="redwu"
                :class="{'active':p2.checked}"
                @click="select(index1,index2)"
                :key="p2.id"
              >{{ p2.name }}</div>
            </div>
          </div>
        </div>
        <div class="goumsl">
          <p>购买数量</p>
          <van-stepper v-model="value" />
        </div>
        <div class="shop">
          <button>立即购买</button>
        </div>
      </van-popup>
    </van-goods-action>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
      lbjlist: [], //内容列表
      pics: [], //图片列表
      lbjtitle: [], //描述列表
      i: 0, //点击切换样式
      //   shopm: ["商品介绍", "商品评价"],
      lbjshuj: [],
      loading: true,
      tancc: false,
      show: false,
      show2: false,
      shouc: true,
      //购买数量数值
      value: 1,
      ps: [],
      yangs: 0
    };
  },
  created() {
    //创建后
    //获取详情接口
    this.$http
      .post(
        "https://api.it120.cc/small4/shop/goods/detail?id=" +
          this.$route.query.id
      )
      .then(msg => {
        // console.log(msg.data.data);

        if (msg.data.msg == "success") {
          this.lbjlist = msg.data.data;
          this.pics = msg.data.data.pics;
          this.lbjtitle = msg.data.data.basicInfo;
          // this.detail = msg.data.data;
          // console.log(this.lbjlist);
          this.loading = false;

          var arr = this.lbjlist.properties;  // 获取总数据
          arr.forEach(item => {
            item.childsCurGoods.forEach(a => {
              a.checked = false;
            });
          });

          this.ps = arr;
        }

        //   console.log(msg.data.data.basicInfo)
      });
    //获取评价接口
    this.$http
      .post(
        "https://api.it120.cc/small4/shop/goods/reputation?goodsId=" +
          this.$route.query.id
      )
      .then(res => {
        // console.log(res);
        this.lbjshuj = res.data.data;
      });
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    qieh(index) {
      this.i = index;
    },
    onClick(name, title) {
      Toast(title);
    },
    back() {
      this.$router.go(-1);
    },
    gouwuc() {
      this.$router.push("/shop");
    },
    //购物车弹出层
    showPopup() {
      this.show = !this.show;
     
    },
    //立即购买弹出层
    showPopup2() {
      this.show2 = !this.show2;
    },

    //收藏显示隐藏
    xingx() {
      this.shouc = !this.shouc;
    },
    //点击加里面加购时的边框
    select(i1, i2) {
      
      this.ps[i1].childsCurGoods.forEach((item, index) => {
        if (index == i2) {
          item.checked = true;
          this.$set(this.ps[i1].childsCurGoods, index, item);
        } else {
          item.checked = false;
          this.$set(this.ps[i1].childsCurGoods, index, item);
        }
      });
    },
    jiag(){
      // alert(11111)
      let t = localStorage.getItem("token")
      // console.log(t)
      if(t){
        // alert(111)
        //把数据添加到购物车
        let p0=[]
        this.ps.forEach((item)=>{
            let r = item.childsCurGoods.filter((p)=>{
              return p.checked
            })
           p0 = p0.concat(r)
        })
        // console.log(p0)
        if(p0.length<this.ps.length){
          this.$notify({ type: 'warning', message: '请选择商品规格' });
          return false
        }
        let obj={
          pic:this.lbjlist.basicInfo.pic,
          name:this.lbjlist.basicInfo.name,
          price:this.lbjlist.basicInfo.minPrice,
          num:this.value,
          pro :p0,
          checked: false,
          id:this.lbjlist.basicInfo.id
          // ps:
        }
        this.$store.commit("addCart",obj)
        // this.$router.push("/shop")
         Toast.success('添加成功');
      }else{
        this.$router.push('/login')
      }
    }
  }
};
</script>

<style lang="less">
.wrapper {
  font-size: 0.2rem;

  .active {
    border: 0.02rem solid #ff0000 !important;
  }

  .head {
    width: 100%;
    height: 100%;
    .van-swipe {
      width: 100%;
      overflow: hidden;
      .van-swipe-item {
        width: 100%;
        height: 5.2rem;
        img {
          width: 100%;
          height: 5.2rem;
        }
      }
    }
    > .fanh {
      width: 0.36rem;
      height: 0.36rem;
      position: fixed;
      left: 0.1rem;
      top: 0.1rem;
      padding: 0.03rem;
      border-radius: 50%;
      border: 0.02rem solid #fff;
      text-align: center;
      line-height: 0.36rem;
      > p {
        width: 100%;
        height: 100%;
        text-align: center;
        background: white;
        border-radius: 50%;
        position: relative;
        .van-icon {
          font-size: 0.3rem;
          color: #666;
          position: absolute;
          top: 0.02rem;
          left: 0.02rem;
        }
      }
    }
    .biaot {
      width: 100%;
      height: 1.24rem;
      font-size: 0.2rem;
      border-bottom: 0.14rem solid #f5f5f5;
      .miaos {
        font-size: 0.22rem;
        color: #464646;
        margin-top: 0.3rem;
        margin-left: 0.2rem;
      }
      .jies {
        font-size: 0.16rem;
        color: #b8b8b8;
        margin-top: 0.16rem;
        margin-left: 0.2rem;
      }
      .price {
        width: 100%;
        height: 0.6rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 0.2rem;
        box-sizing: border-box;
        .dij {
          font-size: 0.14rem;
          color: #cc0c0c;
          width: 1.5rem;
          margin-top: 0.16rem;
        }
        .kc {
          font-size: 0.14rem;
          color: #b8b8b8;
        }
      }
    }
  }

  .content {
    li {
      width: 100%;
      display: flex;
      flex-direction: column;
      box-sizing: border-box;
      .m-attrList {
        width: 100%;
        margin-top: 0.1rem;
        > li {
          padding: 0 0.2rem;
          line-height: 0.4rem;
        }
      }
      .img-lazyload {
        height: 0;
      }
      //   padding-left: 0.2rem;
      //   box-sizing: border-box;
      img {
        width: 100% !important;
      }
    }
  }
  .pingj {
    width: 100%;
    li {
      height: 1.54rem;
      padding: 0 0.2rem;
      border-bottom: 0.01rem solid #e9e9e9;
      display: flex;
      > i {
        font-size: 0.8rem;
        margin-top: 0.18rem;
      }
      > div {
        margin-left: 0.14rem;

        > div:nth-of-type(1) {
          display: flex;
          align-items: center;
          margin-top: 0.18rem;

          p:nth-of-type(1) {
            font-size: 0.16rem;
          }

          p:nth-of-type(2) {
            width: 0.48rem;
            height: 0.32rem;
            border-radius: 0.06rem;
            color: white;
            text-align: center;
            line-height: 0.32rem;
            background: #b7282e;
            font-size: 0.16rem;
            margin-left: 0.2rem;
          }
        }
        > p:nth-of-type(1) {
          font-size: 0.16rem;
          margin-top: 0.1rem;
        }

        > div:nth-of-type(2) {
          display: flex;
          margin-top: 0.2rem;

          > p {
            font-size: 0.12rem;
            color: #b2b2b2;
          }

          > p:nth-of-type(2) {
            margin-left: 0.25rem;
          }
        }
      }
    }
  }
  .van-goods-action-icon__icon {
    font-size: 22px;
  }
  .gouwc-detail {
    height: 1.23rem;
    border-bottom: 0.01rem solid #e9e9e9;
    display: flex;
    .detail-tu {
      width: 0.85rem;
      height: 0.85rem;
      margin-left: 0.2rem;
      margin-top: 0.2rem;
    }
    .detail-title {
      width: 3.5rem;
      margin-left: 0.16rem;
      > p:nth-child(1) {
        font-size: 0.17rem;
        margin-top: 0.3rem;
      }
      > p:nth-child(2) {
        font-size: 0.2rem;
        margin-top: 0.3rem;
        color: #d00000;
      }
    }
  }
  .lieb {
    padding: 0.3rem 0;
    border-bottom: 0.01rem solid #e9e9e9;
    .lieb-ming {
      > p {
        font-size: 0.2rem;
        margin-left: 0.2rem;
        margin-top: 0.1rem;
      }
      > .lieb-leib {
        display: flex;
        flex-wrap: wrap;
        > div {
          padding: 0.1rem 0.2rem;
          border-radius: 0.08rem;
          margin-top: 0.1rem;
          margin-left: 0.1rem;
          border: 0.02rem solid #e9e9e9;
        }
      }
    }
  }
  .goumsl {
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: 1.2rem;
    align-items: center;
    padding: 0 0.2rem;
    box-sizing: border-box;
    > p {
      font-size: 0.2rem;
    }
    .van-stepper__minus,
    .van-stepper__plus {
      width: 0.6rem;
      height: 0.6rem;
    }
    .van-stepper__input {
      width: 0.6rem;
      height: 0.6rem;
    }
  }
  .shop {
    width: 100%;
    height: 0.6rem;
    button {
      width: 100%;
      height: 0.6rem;
      border: none;
      outline: none;
      background: #b7282e;
      color: #fff;
      font-weight: bold;
    }
  }
}
</style>



