<template>
  <div class="detail">
    <div class="head">
        <van-icon @click="back" size="20" name="arrow-left" />
        <p>专栏详情</p>
        <p></p>
    </div>
    <div class="title">{{ shuj.title }}</div>
    <div class="photo">
      <img :src="shuj.pic" alt />
    </div>
    <div>
      <p v-html="shuj.content"></p>
    </div>
  </div>
</template>

<script>
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
      shuj: []
    };
  },
  created() {
    //创建后
    console.log("get传值", this.$route.query.id);
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
    this.$http.get("cms/news/detail?id=" + this.$route.query.id).then(res => {
      console.log(res);
      this.shuj = res.data.data;
    });
    // this.$http.get("cms/news/detail?id=" + this.$route.params.id).then(res => {
    //   console.log(res);
    //   this.shuj = res.data.data;
    // });
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    back(){
        this.$router.go(-1)
    }
  }
};
</script>

<style scoped lang="less">
.detail {
    .head{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 0.2rem;
    }
  .title {
    font-size: 0.3rem;
    color: #333333;
    width: 100%;
    text-align: center;
    margin-top: 0.4rem;
    margin-bottom: 0.2rem;
  }
  .photo {
      width: 100%;
      text-align: center;
    img {
      width: 4.85rem;
      height: 3.13rem;
    }
  }

  p {
    font-size: 0.2rem;
    color: #333333;
    margin-top: 0.1rem;
    text-indent: 0.3rem;
    line-height: 0.48rem;
  }
}
</style>



