import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/pages/Home'
import Shouy from '@/pages/Shouy'
import Fenl from '@/pages/Fenl'
import Gouw from '@/pages/Gouw'
import Ger from '@/pages/Ger'
import Detail from '@/pages/Detail'
import Zhuce from '@/pages/Zhuce'
import Login from '@/pages/Login'
import Fenlitem from '@/pages/Fenlitem'
import Fenldetail from '@/pages/Fenldetail'
import Shop from '@/pages/Shop'
import Qrdd from '@/pages/Qrdd'
import Dizlist from '@/pages/Dizlist'
import Newdiz from '@/pages/Newdiz'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect: "/home"
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      redirect:"/home/shouy",
      children:[
        {
          path: 'shouy',
          name: 'Shouy',
          component: Shouy,
        },
        {
          path: 'fenl',
          name: 'Fenl',
          component: Fenl,
        },
        {
          path: 'gouw',
          name: 'Gouw',
          component: Gouw,
        },
        {
          path: 'ger',
          name: 'Ger',
          component: Ger,
        }
      ]
    },
    {
      path: '/detail',
      name: 'Detail1',
      component: Detail,
    },
    {
      path: '/detail/:id',
      name: 'Detail2',
      component: Detail,
    },
    {
      path: '/zhuce',
      name: 'Zhuce',
      component: Zhuce,
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/fenlitem',
      name: 'Fenlitem',
      component: Fenlitem,
    },
    {
      path: '/fenldetail',
      name: 'Fenldetail',
      component: Fenldetail,
    },
    {
      path: '/shop',
      name: 'Shop',
      component: Shop,
    },
    {
      path: '/qrdd',
      name: 'Qrdd',
      component: Qrdd,
    },
    {
      path: '/dizlist',
      name: 'Dizlist',
      component: Dizlist,
    },
    {
      path: '/newdiz',
      name: 'Newdiz',
      component: Newdiz,
    }
  ]
})



