<template>
  <div class="wrap">
    <div class="box">
      <p class="title">新用户注册</p>
      <p class="zi">很高兴您将成为商城的会员（注册只需一步）</p>
      <p class="wenb" :class="{'action':gais,'action1':gais1 }">
        <input type="text" placeholder="手机号码" v-model="phone" @blur="phone1" />
        <span style="color: red;" v-show="bit">必填</span>
        <van-icon name="graphic" />
      </p>
      <p class="wenbtwo" :class="{'action':mimg,'action1':mimg1 }">
        <input type="password" placeholder="密码" v-model="pwd" v-if="mixian" @blur="mim" />
        <input type="text" placeholder="密码" v-model="pwd" v-else @blur="mim" />
        <span style="color: red;" v-show="bit1">必填</span>
        <van-icon name="closed-eye" @click="dianx" v-if="mixian" />
        <van-icon name="eye-o" @click="diany" v-else />
        
      </p>
      <p class="wenbtwo" :class="{'action':qrmigai,'action1':qrmigai1 }">
        <input type="password" placeholder="确认密码" v-model="querpwd" v-if="miqx" @blur="qrmim" />
        <input type="text" placeholder="确认密码" v-model="pwd" v-else @blur="qrmim" />
        <van-icon name="closed-eye" @click="dianqx" v-if="miqx" />
        <van-icon name="eye-o" @click="dianqy" v-else />
        <!-- <span style="color: red; display:none;">必填</span> -->
      </p>
      <p class="wenbtwo">
        <input type="text" placeholder="用户名" />
        <!-- <span style="color: red; display:none;">必填</span> -->
        <van-icon name="manager" />
      </p>
      <p class="wenbtwotux">
        <input type="text" v-model="piccode" />
        <img :src="imgcode + key" alt @click="updateImg" />
      </p>
      <p class="chengs">
        <!-- <input type="text" /> -->
        <van-field
          readonly
          clickable
          :value="value"
          @click="showPicker = true"
        />
      </p>
      <van-popup v-model="showPicker" round position="bottom">
        <van-picker
          show-toolbar
          :columns="columns"
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
      <!-- <div class="cs">
          <van-area title="标题" :area-list="areaList" :columns-num="2"
          :columns-placeholder="['请选择', '请选择', '请选择']" />
      </div>-->
      <p class="wenbthree">
        <input type="text" v-model="code" />
        <span class="yanz" @click="getCode" v-if="yzm">获取验证码</span>
        <span class="yanz" v-else>{{ shij }}秒后重试</span>
      </p>
      <p class="ann" @click="zhuc">立即注册</p>
      <p class="wei">已有账号？ <span @click="dengl">立即登录</span></p>
    </div>
  </div>
</template>

<script>
const citys = {
  浙江省: ['杭州市', '宁波市', '温州市', '嘉兴市', '湖州市'],
  福建省: ['福州市', '厦门市', '莆田市', '三明市', '泉州市'],
};
import { v4 as uuidv4 } from "uuid";
import qs from "qs";
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据
      imgcode: "https://api.it120.cc/small4/verification/pic/get?key=",
      key: uuidv4(),
      phone: "", //手机号绑定
      piccode: "", //图片验证绑定
      pwd: "", //密码数据绑定
      code: "", //验证码绑定
      querpwd: "", //确认密码数据绑定
      mixian: true, //密码显示与隐藏
      miqx: true, //确认密码显示与隐藏
      shij: 60,    //倒计时时间
      yzm: true, //验证码
      timer: null,  //倒计时所用参数
      //手机号改变色
      gais: false,
      gais1: false,
      //密码改变色
      mimg: false,
      mimg1: false,
      //确认密码改变色
      qrmigai: false,
      qrmigai1: false,
      //城市
      value: '',
      showPicker: false,
      columns: [{ values: Object.keys(citys) },{ values: citys['浙江省'] }],
      //必填项
      bit:false,
      bit1:false
    };
  },
  created() {
    //创建后
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    updateImg() {
      // this.imgcode="https://api.it120.cc/small4/verification/pic/get?key="+uuidv4()
      this.key = uuidv4();
    },
    //短信验证码验证
    getCode() {
      let data = {
        mobile: this.phone,
        picCode: this.piccode,
        key: this.key
      };
      this.$http
        .post(
          "https://api.it120.cc/small4/verification/sms/get",
          qs.stringify(data)
        )
        .then(msg => {
          console.log(msg);
        });
      this.yzm = false;
      this.timer = setInterval(() => {
        if (this.shij > 0 && this.shij <= 60) {
          this.shij--;
        } else {
          this.yzm = true;
          clearInterval(this.timer);
          this.shij = 60;
          this.timer = null;
        }
      }, 1000);
    },
    //注册验证
    zhuc() {
      let data = {
        mobile: this.phone,
        pwd: this.pwd,
        code: this.code
      };
      this.$http
        .post("https://api.it120.cc/small4/user/m/register", qs.stringify(data))
        .then(msg => {
          console.log(msg);
        });
    },
    //切换密码显示隐藏
    dianx() {
      this.mixian = false;
    },
    diany() {
      this.mixian = true;
    },
    //切换确认密码显示隐藏
    dianqx() {
      this.miqx = false;
    },
    dianqy() {
      this.miqx = true;
    },
    //手机号验证是否为空
    phone1() {
      if (this.phone == "") {
        // 警告通知
        this.gais = true;
        this.bit = true;
        this.$notify({ type: "danger", message: "请输入正确的手机号" });
      } else if (this.phone != "") {
        this.gais1 = true;
      }
    },
    //密码验证是否为空
    mim() {
      if (this.pwd == "") {
        // 警告通知
        this.mimg = true;
        this.bit1 = true;
        this.$notify({ type: "danger", message: "请输入正确密码" });
      } else if (this.pwd != "") {
        this.mimg1 = true;
      }
    },
    //确认密码验证是否为空
    qrmim() {
      if (this.querpwd == "" || this.pwd !== this.querpwd) {
        // 警告通知
        this.qrmigai = true;
        this.$notify({ type: "danger", message: "请输入正确密码" });
      } else if (this.querpwd != "") {
        this.qrmigai1 = true;
      }
    },
     onConfirm(value) {
      this.value = value;
      this.showPicker = false;
    },
    //点击登录跳转登录页
    dengl(){
      this.$router.push("/Login")
    }
  }
};
</script>

<style scoped lang="less">
.wrap {
  padding: 0.26rem;
  .box {
    width: 3.8rem;
    border: 0.01rem solid #f5f5f5;
    padding: 0 0.44rem;
    font-size: 0.2rem;
    min-height: 10rem;
    .title {
      margin-top: 0.5rem;
      font-size: 0.3rem;
    }
    .zi {
      font-size: 0.2rem;
      color: #909090;
      margin-top: 0.5rem;
      margin-bottom: 0.23rem;
    }
    .wenb {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.44rem;
      display: flex;
      justify-content: space-around;
      align-items: center;
      border: 0.01rem solid #f5f5f5;
      input {
        border: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        outline: none;
      }
      .van-icon {
        font-size: 0.22rem;
        color: #d1cfce;
      }
    }
    .action {
      border: 0.01rem solid rgb(225, 44, 30) !important;
      // background: rgb(255, 233, 228) !important;
    }
    .action1 {
      border: 0.01rem solid rgb(56, 219, 36) !important;
      background: rgb(225, 255, 223) !important;
      input {
        background: rgb(225, 255, 223) !important;
      }
    }
    .wenbtwo {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      justify-content: space-around;
      align-items: center;
      border: 0.01rem solid #f5f5f5;
      input {
        border: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        outline: none;
      }
      .van-icon {
        font-size: 0.22rem;
        color: #d1cfce;
      }
    }
    .chengs {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      justify-content: space-around;
      align-items: center;
      border: 0.01rem solid #f5f5f5;
      .van-field {
        border: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        outline: none;
        width: 3.72rem;
        // display: none;
      }
    }
    .wenbtwotux {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      border: 0.01rem solid #f5f5f5;
      display: flex;
      justify-content: space-between;
      align-items: center;
      input {
        border: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        outline: none;
        width: 1.5rem;
        padding-left: 0.1rem;
      }
      img {
        font-size: 0.22rem;
        color: #d1cfce;
        width: 2rem;
        height: 0.66rem;
      }
    }
    .wenbthree {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      border: 0.01rem solid #f5f5f5;
      input {
        width: 1.7rem;
        height: 0.66rem;
        border: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        outline: none;
        // text-align: center;
        line-height: 0.66rem;
        padding: 0 0.1rem;
      }
      .yanz {
        display: inline-block;
        width: 1.7rem;
        border-left: 0.01rem solid #e1e1e1;
        height: 0.66rem;
        text-align: center;
        line-height: 0.66rem;
        cursor: pointer;
      }
    }
    .ann {
      width: 2.91rem;
      height: 0.57rem;
      background: linear-gradient(90deg, #5ea6f8, #bb87f6);
      text-align: center;
      line-height: 0.57rem;
      margin: 0 auto;
      margin-top: 0.24rem;
      color: #fff;
      border-radius: 0.06rem;
      letter-spacing: 0.06rem;
    }
    .wei {
      width: 2.91rem;
      font-size: 0.22rem;
      color: #549ff9;
      margin: 0 auto;
      text-align: center;
      margin-top: 0.32rem;
    }
  }
}
</style>



