<template>
  <div class="kan">
    <p class="title">
      <span>全民砍价</span>
      <van-icon class="small" name="arrow" />
    </p>
    <van-loading v-if="isShow" type="spinner" color="#1989fa" />
    <ul class="list" v-else>
      <kj-item :kanj="item" v-for="(item,index) in lbjkj" :key="item.id" v-show="index>2"></kj-item>
    </ul>
  </div>
</template>

<script>
import KjItem from "@/components/KjItem";
export default {
  data() {
    return {
      lbjkj: [],
      isShow:true,
    };
  },
  created() {},
  components: {
    KjItem
  },
  mounted() {    
    this.$http
      .get("/shop/goods/kanjia/list")
      .then(res => {
        console.log(res);
        // this.lbjkj = res.data.data.goodsMap;
        this.isShow=false
        for(let i in res.data.data.goodsMap){
          this.lbjkj.push(res.data.data.goodsMap[i])
        }
      });
  },
  methods: {}
};
</script>

<style scoped lang="less">
.kan {
  border-bottom: 0.2rem solid #f5f5f5;
  .title {
    display: flex;
    height: 0.65rem;
    justify-content: center;
    align-items: center;
    border-bottom: 0.01rem solid #e9e9e9;
    > span {
      font-size: 0.24rem;
    }
    > .small {
      font-size: 0.2rem;
    }
  }
  .list {
    font-size: 0.18rem;
    width: 100%;
    box-sizing: border-box;
  }
}
</style>



