<template>
  <div class="wrap">
    <p class="ger">个人中心</p>
    <button @click="logout()">退出</button>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  created() {},
  mounted() {},
  methods: {
    logout() {
      localStorage.removeItem("token");
      this.$router.push("/Login");
    }
  }
};
</script>

<style scoped lang="less">
.wrap {
  .ger {
    font-size: 0.16rem;
    overflow: hidden;
  }
  button{
      font-size: 0.16rem;
  }
}
</style>



