<template>
  <div class="tuij">
    <p class="title">
      <span>人气推荐</span>
      <van-icon class="small" name="arrow" />
    </p>
    <ul class="tj">
      <tj-item v-for="(item,index) in tjlist" :tj="item" :key="item.id" v-show="index>8&&index<13"></tj-item>
    </ul>
  </div>
</template>

<script>
import TjItem from "@/components/TjItem";
export default {
  data() {
    return {
      tjlist: [],
      i:0
    };
  },
  created() {},
  components: {
    TjItem
  },
  mounted() {
    this.$http.get("/shop/goods/list").then(res => {
      console.log(res)
      // this.tjlist = res.data.data;
     for(let i in res.data.data){
        this.tjlist.push(res.data.data[i])
     }
    });
  },
  methods: {}
};
</script>

<style scoped lang="less">
.tuij {
  border-bottom: 0.18rem solid #f5f5f5;
  .title {
    width: 100%;
    height: 0.54rem;
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      font-size: 0.22rem;
    }
    .small {
      font-size: 0.22rem;
    }
  }
  .tj {
    width: 100%;
    padding: 0 0.16rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    box-sizing: border-box;
    // height: 8rem;
  }
}
</style>



