<template>
  <div class="wrap">
    <div class="xinx">
      <div class="head">
        <van-icon name="arrow-left" @click="go" />
        <p>确认订单</p>
        <p></p>
      </div>
      <div class="diz">
        <van-icon name="map-marked" color="#dbdadf" />
        <!-- <van-icon name="plus" class="jia" /> -->
        <!-- <div class="xinz">新增收货地址</div> -->
        <div class="lic" @click="goAddlist">
          <p>
            <span>{{ shuj.linkMan }}</span>
            <span>{{ shuj.mobile }}</span>
          </p>
          <p>{{ shuj.address }}</p>
        </div>
        <van-icon class="right" name="arrow" color="#dbdadf" />
      </div>
      <img
        src="data:image/png;base64,"
        alt
      />
    </div>
    <div class="lieb">
      <p>商品列表</p>
    </div>
    <ul class="list">
      <li v-for="item in $store.getters.qrdd" :key="item.id">
        <img :src="item.pic" alt />
        <div class="title1">
          <p>{{ item.name }}</p>
          <p>
            <span v-for="p in item.pro" :key="p.id">{{ p.name }}</span>
            <span></span>
          </p>
        </div>
        <div class="title2">
          <p>￥{{ item.price }}</p>
          <p>x{{ item.num }}</p>
        </div>
      </li>
    </ul>
    <div class="kuaid">
      <p>配送方式</p>
      <p>快递</p>
    </div>
    <div class="beiz">
      <p>备注</p>
      <input type="text" placeholder="如需备注请输入" />
    </div>
    <div class="jine">
      <p>商品金额</p>
      <p>￥{{ $store.getters.jiag }}</p>
    </div>
    <div class="jies">
      <p>合计：￥{{ $store.getters.jiag }}</p>
      <p>提交订单</p>
    </div>
  </div>
</template>

<script>
import qs from "qs";
export default {
  props: {}, // 组件参数 接收来自父组件的数据
  data() {
    return {
      //模拟数据

      shuj: {}
    };
  },
  created() {
    //创建后
    this.$http
      .get(
        "https://api.it120.cc/small4/user/shipping-address/default?token=" +
          localStorage.getItem("token")
      )
      .then(msg => {
        console.log(msg);
        if (msg.data.msg == "success") {
          this.shuj = msg.data.data;
        }
      });
  },
  components: {}, // 局部注册的组件
  mounted() {
    //加载后
  },
  watch: {}, // 负责监听
  computed: {}, // 计算属性
  methods: {
    //方法集合
    go(){
        this.$router.go(-1)
    },
    goAddlist(){
        this.$router.push("/dizlist")
    }
  }
};
</script>

<style scoped lang="less">
.wrap {
  font-size: 0.22rem;
  .head {
    display: flex;
    justify-content: space-between;
    padding: 0 0.2rem;
    box-sizing: border-box;
    height: 0.7rem;
    align-items: center;
  }
  .xinx {
    border-bottom: 0.14rem solid #e8e8e8;
  }
  .diz {
    display: flex;
    justify-content: left;
    height: 0.8rem;
    align-items: center;
    padding: 0 0.2rem;
    box-sizing: border-box;
    .jia {
      width: 0.44rem;
      height: 0.44rem;
      border-radius: 0.06rem;
      text-align: center;
      background: #3283f9;
      line-height: 0.44rem;
      color: #fff;
    }
    .xinz {
      width: 4.13rem;
      height: 0.58rem;
      margin-left: 0.32rem;
      margin-top: 0.32rem;
    }
    .lic {
      margin-left: 0.3rem;
      display: flex;
      height: 0.8rem;
      justify-content: space-around;
      flex-direction: column;
      font-size:0.18rem;
    }
    .right {
      position: fixed;
      right: 0.2rem;
    }
    img {
      width: 100%;
      height: 0.02rem;
      //   display: block;
    }
  }
  .lieb {
    display: flex;
    height: 0.7rem;
    align-items: center;
    padding-left: 0.2rem;
    font-size: 0.18rem;
    box-sizing: border-box;
    border-bottom: 0.04rem solid #f5f5f5;
  }
  .list {
    border-bottom: 0.15rem solid #f5f5f5;
    > li {
      height: 1.53rem;
      border-bottom: 1px solid #e8e8e8;
      display: flex;
      align-items: center;
      > img {
        width: 1.12rem;
        height: 1.12rem;
        display: block;
        margin-left: 0.2rem;
      }
      .title1 {
        margin-left: 0.12rem;
        width: 3.06rem;
        height: 1.12rem;
        > p:nth-child(1) {
          font-size: 0.18rem;
          margin-top: 0.1rem;
        }
        > p:nth-child(2) {
          font-size: 0.18rem;
          margin-top: 0.55rem;
          color: #999;
        }
      }
      .title2 {
        height: 1.34rem;
        > p:nth-child(1) {
          font-size: 0.18rem;
          margin-top: 0.1rem;
        }
        > p:nth-child(2) {
          font-size: 0.18rem;
          margin-top: 0.6rem;
          margin-left: 0.29rem;
          color: #999;
        }
      }
    }
  }
  .kuaid {
    display: flex;
    height: 0.6rem;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.2rem;
    box-sizing: border-box;
    font-size: 0.18rem;
    border-bottom: 0.01rem solid #e5e5e5;
  }
  .beiz {
    display: flex;
    height: 0.7rem;
    padding: 0 0.2rem;
    align-items: center;
    border-bottom: 0.15rem solid #f5f5f5;
    > p {
      font-size: 0.18rem;
    }
    > input {
      width: 3.1rem;
      height: 0.2rem;
      font-size: 0.18rem;
      color: #333;
      margin-left: 0.76rem;
      border: none;
    }
  }
  .jine {
    display: flex;
    height: 0.6rem;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.2rem;
    box-sizing: border-box;
    font-size: 0.18rem;
    border-bottom: 0.15rem solid #f5f5f5;
  }
  .jies {
    width: 100%;
    background: white;
    height: 0.69rem;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    > p:nth-child(1) {
      font-size: 0.2rem;
      color: #d00000;
      line-height: 0.69rem;
      margin-left: 2rem;
    }
    > p:nth-child(2) {
      width: 1.73rem;
      height: 0.69rem;
      background: #b7282e;
      color: white;
      font-size: 0.22rem;
      text-align: center;
      line-height: 0.69rem;
    }
  }
}
</style>



